.toggle-button {
    position: absolute;
    right: 0px;
  }
  
  .toggle-button svg{
    font-size: 22px;
  }
  
  .toggle-button button {
    background-color: transparent;
    color: #fff;
    border: none;
    cursor: pointer;
    font-size: 16px;
  }
  
  .toggle-button button:focus {
    outline: none;
  }
  
  .toggle-btn {
    display: none;
    line-height: 76px;
    padding-right: 2em;;
  }
  
  .sidebar {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: #111;
    color: #fff;
    transition: transform .5s ease-in-out;
    transform: translateX(100%);
    text-align: center;
  }
  
  .sidebar.open {
    transform: translateX(0);
  }
  
  .close-btn {
    position: absolute;
    top: 25px;
    right: 25px;
    background: none;
    border: none;
    color: #fff;
    cursor: pointer;
  }
  
  .close-btn  svg{
    font-size: 22px;
  }
  
  @media (max-width: 768px) {
    .toggle-btn {
      display: block;
    }

    .sidebar .logo svg{
      margin-top: 50px;
      width: 70px;
    }
  
    .menu {
      display: none !important;
    }

    .togglemenu ul {
      padding: 0;
      list-style: none;
    }
    
    .togglemenu li {
        line-height: 40px;
        padding: 5px 14px;
      }
      
    .togglemenu a {
      color: #fff;
      text-decoration: none;
      font-size: 20px;
    }

    .togglemenu svg{
      padding-right: 10px;
      font-size: 14px;
    }
  }
  